html{
    width: 100%;
}
body{
    width: 100%;
    margin: 0;
    padding: 0;
    border: 0;
    font-weight: inherit;
    font-style: inherit;
    font-family: 'Roboto-Regular';
    font-size: 16px;
    text-align: left;
    vertical-align: baseline;
    // background-image: url(../img/background.jpg);
    // background-repeat: no-repeat;
    background-color: #F6F4F9;
    // background-size: 100%;
}

::placeholder {
    opacity: 1;
}

a {
    text-decoration: none;
    &:hover, &:focus, &:active{
        text-decoration: none;
    }
}

input{
    outline: none;
    &:hover, &:focus, &:active{
        outline: none;
    }
}

textarea{
    outline: none;
    &:hover, &:focus, &:active{
        outline: none;
    }
}

%clearfix {
    *zoom: 1;
    &:after {
        content: '';
        display: table;
        line-height: 0;
        clear: both;
    }
}

.actTab {
    display: block;
    /*height: auto;*/
}

