.featured-img {
    img {
        width: 100%;
    }
}

.read-also__title {
    font-family: "Roboto-Black", sans-serif;
    font-weight: normal;
    font-size: 25px;
    letter-spacing: -0.13px;
}

.partners {
    color: #ccc;
}

.header__content {
    margin-bottom: 10px;;
}

.active-rubric {
    border-bottom: 2px solid #ff6900 !important;
}

.news-main__title,
#contests__form,
.embassies__city,
.tv__schedule-title {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    flex-wrap: wrap;
    border-bottom: 2px solid #ff6900;
    h2 {
        margin-top: 0;
        margin-bottom: 0;
        border: none;
    }

    #select {
        margin-right: 20px;
        margin-bottom: 10px;
        @include breakpoint(max-width 576px) {
            margin-right: 5px;
        }
        .active-country {
            padding-right: 25px;
            font-family: 'Roboto-Bold', sans-serif;
            font-size: 14px;
            color: #040000;
            cursor: pointer;
            &:after {
                content: '';
                width: 10px;
                height: 6px;
                margin-left: 15px;
                position: absolute;
                bottom: 16px;
                background-image: url(../img/arrow-down.png);
                background-repeat: no-repeat;
            }
            @include breakpoint(max-width 576px) {
                padding-right: 5px;
                &:after {
                    margin-left: 5px;
                }
            }
        }
        .opened-list {
            &:after {
                content: '';
                background-image: url(../img/arrow-up.png);
            }
        }
        .country-select {
            padding: 20px;
            position: absolute;
            right: 0;
            display: none;
            list-style: none;
            background-color: white;
            li {
                width: 25%;
                height: 15px;
                padding: 0;
                margin: 5px;
                cursor: pointer;
                line-height: 1.2;
                @include breakpoint(max-width 768px) {
                    width: 30%;
                    height: auto;
                }

                @include breakpoint(max-width 576px) {
                    width: 50%;
                }

                .country {
                    padding: 5px 0;
                    font-family: 'Gilroy-Regular', sans-serif;
                    font-size: 14px;
                    color: #040000;
                    border: none;
                    text-transform: none;
                    &:hover {
                        font-family: 'Gilroy-Black', sans-serif;
                        border: none;
                        color: #f06226;
                    }
                }
                .selected-country {
                    font-family: 'Gilroy-Black', sans-serif;
                    color: #f06226
                }
            }
        }
        .expand-country {
            width: 95.1%;
            height: 325px;
            display: flex;
            flex-direction: column;
            flex-wrap: wrap;
            z-index: 2;
            @include breakpoint(max-width 768px) {
                height: 100vh;
            }
            @include breakpoint(max-width 576px) {
                height: 150vh;
            }
        }
    }
}

.news-main__nav {
    .news-main__nav-menu {
        margin: 5px 0;
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        a {
            padding: 7px 5px 5px;
            font-family: 'Roboto-Bold', sans-serif;
            font-size: 12px;
            text-transform: uppercase;
            color: #040000;
            border: 1px solid transparent;
            &:hover, &:focus {
                border-color: #ff6900;
            }
            &:active {
                background-color: white;
            }
        }
        .active-news {
            border-color: #ff6900;
            background-color: white;
        }
    }
}

.news-blocks {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    color: white;
    text-transform: uppercase;
    img {
        width: 100%;
        height: 100%;
        border: 1px solid black;
        box-sizing: border-box;
        @include breakpoint(max-width 576px) {
            height: auto;
        }
    }

    .big-block {
        max-width: 405px;
        min-width: 275px;
        margin: 5px 0;
        position: relative;
        @include breakpoint(max-width 576px) {
            max-width: 100%;
            width: 100%;
        }
        .big-block__rubric {
            h3 {
                margin: 0;
                padding: 7px 20px 5px;
                position: absolute;
                top: 0;
                background-color: rgba(0,0,0,.65);
                font-family: 'Gilroy-Black',sans-serif;
                font-weight: normal;
                font-size: 13px;
            }
        }

        .big-block__title,
        .big-block__title-top {
            width: 100%;
            box-sizing: border-box;
            height: 40%;
            padding: 10px;
            display: flex;
            align-items: flex-end;
            flex-wrap: wrap;
            position: absolute;
            bottom: 4px;
            background: linear-gradient(to top, hsla(240, 81%, 6%, 0.75), transparent);
            span {
                padding-right: 10px;
                font-family: 'Lato-Medium',sans-serif;
                font-size: 8px;
            }

            .big-block__views {
                font-size: 12px;
            }

            h4{
                position: relative;
                top: 20px;
                margin: 0;
                @include breakpoint(max-width 576px) {
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
                a {
                    font-family: 'Gilroy-Bold',sans-serif;
                    font-weight: normal;
                    font-size: 17px;
                    color: #fff;
                }
            }
        }

        .big-block__title-top {
            padding: 0 0 10px 15px;
            span {
                font-size: 12px;
                text-transform: none;
            }
        }
    }

    .height260 {
        max-width: 403px;
        height: 260px;
        @include breakpoint(max-width 576px) {
            height: auto;
        }

        .big-block__title {
            bottom: 0;
            @include breakpoint(max-width 576px) {
                bottom: 3px;
            }

            h4 {
                top: 0;
            }
        }
    }

    .small-block__container {
        max-width: 403px;
        min-width: 280px;
        margin: 5px 0;
        position: relative;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        @include breakpoint(max-width 576px) {
            max-width: 100%;
        }
    }

    .small-block {
        width: 195px;
        height: 125px;
        margin: 5px 0;
        position: relative;
        @include breakpoint(max-width 576px) {
            width: 100%;
            height: auto;
        }

        h3 {
            margin: 0;
            padding: 7px 20px 5px;
            position: absolute;
            top: 0;
            background-color: rgba(0,0,0,.65);
            font-family: 'Gilroy-Black',sans-serif;
            font-weight: normal;
            font-size: 11px;
        }

        .small-block__title {
            width: 100%;
            max-height: 60%;
            padding: 10px;
            position: absolute;
            bottom: 0;
            background: linear-gradient(to top, hsla(240, 81%, 6%, 0.75), transparent);
            box-sizing: border-box;
            @include breakpoint(max-width 576px) {
                bottom: 4px;
            }
            h4 {
                margin: 0;
                line-height: .9;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                a {
                    color: white;
                    font-family: 'Roboto-Bold',sans-serif;
                    font-weight: normal;
                    font-size: 9px;
                }
            }

            span {
                margin-right: 5px;
                font-family: 'Roboto-Bold', sans-serif;
                font-size: 10px;
            }
        }
    }
}

.materials__head-tabs .small-block .small-block__title {
    bottom: 0;
}

.read-also__slide {
    margin: 0 auto;

    @include breakpoint(max-width 768px) {
        width: 97%;
    }
    @include breakpoint(max-width 576px) {
        width: 89%;
    }
}

.modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 2; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

.modal__content {
    position: relative;
    right: 100px;
    display: flex;
    flex-direction: column;
    margin: 4% auto; /* 4% from the top and centered */
    padding: 20px;
    max-width: 645px;
    min-height: 690px;
    background-color: white;
    background-size: cover;
    z-index: 3;
    @include breakpoint(max-width 992px) {
        right: 0;
        max-width: 100%;
    }
    iframe {
        display: block;
        margin: 0 auto;
    }

    .modal-ad {
        position: absolute;
        top: 50px;
        right: -240px;
        @include breakpoint(max-width 992px) {
            top: 100%;
            right: 30%;
        }
    }

    .social-likes {
        margin: 20px 0;
    }
}

.modal__header {
    h3 {
        margin-top: 0;
    }
}

.modal__desc {
    display: flex;
    justify-content: space-between;
    @include breakpoint(max-width 768px) {
        justify-content: space-around;
        flex-wrap: wrap;
    }

    .modal__img {
        max-width: 300px;
        width: 50%;
        text-align: center;
        @include breakpoint(max-width 768px) {
            width: 100%;
        }

        img {
            width: 100%;
        }
    }

    .event-desc {
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        @include breakpoint(max-width 768px) {
            width: 100%;
        }
    }
}

.activeModal {
    display: block;
}

.close {
    position: absolute;
    top: 10px;
    right: 20px;
    color: #aaa;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}

.slick-slide,
.video__video {
    img {
        border: 1px solid black;
        box-sizing: border-box;
    }
}
